import {
  Block,
  Main,
  Section,
  Wrapper,
  ScrollContainer,
  BetweenBlock,
  Container,
} from "../components/Layout";
import { useSelector } from "react-redux";
import { selectPosts } from "../redux/reducers/postReducer";
import { H1, H3, H4 } from "../components/Text";
import { ArrowLink } from "../components/Link";
import { Card, ImageCard } from "../components/Card";
import { defaultImage } from "../constants/variable";

export default function HomePage() {
  const posts = useSelector(selectPosts);

  return (
    <Main>
      <Wrapper>
        <H1></H1>
        <H1></H1>
        <H1></H1>
      </Wrapper>
      <Section>
        <ScrollContainer as="ul">
          {posts &&
            posts
              .slice(3, 10)
              .map((post) => (
                <ImageCard
                  as="li"
                  key={post.id}
                  to={`/article/${post.id}`}
                  post={post}
                  $square={"40vmax"}
                  $image={defaultImage}
                />
              ))}
        </ScrollContainer>
      </Section>
      <Wrapper>
        <Section>
          <Container>
            <Block>
              <div>
                <H3></H3>
                <br />
                <H3>Simple but profound.</H3>
              </div>
              <ArrowLink
                to={"/about"}
                className={"link"}
                label={""}
                $hide
              />
            </Block>
            <Block>
              <H4 as="p" $grey700>
                {` React  Redux  Components\n\n Express.js  Node.js AWS Nginx `}
              </H4>
            </Block>
          </Container>
        </Section>
        <Section>
          <BetweenBlock>
            <H3 $grey500></H3>
            <ArrowLink to={"/list"} label={""} />
          </BetweenBlock>
          <Container as="ul">
            {posts &&
              posts
                .slice(0, 3)
                .map((post) => (
                  <Card
                    as="li"
                    key={post.id}
                    to={`/article/${post.id}`}
                    post={post}
                    $width={"32%"}
                    $image={defaultImage}
                  />
                ))}
          </Container>
        </Section>
      </Wrapper>
    </Main>
  );
}
